import $ from 'jquery';

const valueList = $('.currency-selector__value');
let valueSelected = $('.currency-selector__value_selected');

console.log(valueList);

$('.currency-selector').click(function () {
	Array.prototype.forEach.call(valueList, function (el) {
		$(el).addClass('currency-selector__value_selected');
	});
	for (let i = 0; i < valueList.length; i++) {
		$(valueList[i]).click(function (event) {
			event.stopPropagation();
			let currentValue = $(valueList[i]).text();
			let lastValue = $(valueList[0]).text();
			$(valueList[i]).text(lastValue);
			$(valueList[0]).text(currentValue);
			Array.prototype.forEach.call(valueList, function (el) {
				console.log('none');
				if (el !== valueList[0]) {
					$(el).removeClass('currency-selector__value_selected');
				}
			});
		});
	}
	Array.prototype.forEach.call(valueList, function (el) {
		$(el).off();
	});
});
function changeSelectedValue (event) {
	event.stopPropagation();
	let currentValue = $(valueList[i]).text();
	let lastValue = $(valueList[0]).text();
	$(valueList[i]).text(lastValue);
	$(valueList[0]).text(currentValue);
	Array.prototype.forEach.call(valueList, function (el) {
		console.log('none');
		if (el !== valueList[0]) {
			$(el).removeClass('currency-selector__value_selected');
		}
	});
}




